.UtilityBar {
	-ms-flex: 0 0 50px;
	    flex: 0 0 50px;
	background-color: #FF6600;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	z-index: 1000;
	/*column-gap: 20px;  applies to flex only on FF currently,
		so for now stick to the .Ut > div margin stuff below */
}

.UtilityBar > div {
	/* Spacing between flex items */
	margin-right: 10px;
	margin-left: 10px;
}



