.no-maps {
	width:60%;
	left:20%;
	height:100%;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: stretch;
	    align-items: stretch;
}

@media (max-width:650px){
	.no-maps{
		width:90%;
		left:5%;
	}
}
.no-maps p {
	-ms-flex: none;
	    flex: none;
	padding:20px;
	text-align: justify;
	-webkit-user-select: none;  
  	-moz-user-select: none;     
  	-ms-user-select: none;      
  	user-select: none;  
}
#maps {
	background-color: #FF6600;
}

.map0 {  
  width: 100vw;
}

.map-title {
	color: #FF6600;
	font-family: 'Open Sans', sans-serif;
	position: relative;
	text-shadow: black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.1em;
	max-width: 150px;
	-webkit-user-select: none;  
  	-moz-user-select: none;     
  	-ms-user-select: none;      
  	user-select: none;          
	line-height: 1.1em;
}

@media (max-height: 500px) {
	.no-maps p {
		font-size: 1em;
	}
}

@media (min-height: 501px) and (max-height: 850px) {
	.no-maps p {
		font-size: 1.5em;
	}
	.map-title {
		font-size:2em;
	}
	.map-title.long-title {
		font-size:1em;
	}
}

@media (min-height: 851px) and (max-height: 950px) {
	.map-title, .no-maps p {
		font-size:2.5em;
	}
	.map-title.long-title {
		font-size:1.5em;
	}
}

@media (min-height: 951px) {
	.map-title,.no-maps p {
		font-size:3em;
	}
	.map-title.long-title {
		font-size:2em;
	}
}



/*
@media (min-width:800px) {

*/
	.map0, .map1, .map2, .map3, .map5 {
		height:100%;
	}
	.map1 {
		width:100vw;
	}


/* Two maps side by side 
	
	map map
*/

	.map2 {
		width:49.75vw;
	}

	.map2.p0{
		left:0;
	}


	.map2.p1 {
		right:0;
	}


/* Three maps in a single row

	map map map
*/
	.map3 {
		width:33%;
	}

		.map3.p0 {
			left:0;
		}
		.map3.p1 {
			left:33.5%;
		}
		.map3.p2 {
			right: 0;
		}


/* 4 maps arranged 2x2 

	map map
	map map
*/
	.map4 {
		width: 49.75vw;
		height: 49.5%;
	}
	.map4.p0 {
		top:0;
		left:0;
	}
	.map4.p1 {
		top:0;
		right:0;
	}
	.map4.p2 {
		bottom:0;
		left:0;
	}
	.map4.p3 {
		bottom:0;
		right:0;
	}


/*	5 maps arranged 3 x 2

	map map map
	  map map
*/

.map5 {
	height:49.5%;
}
.map5.p0, .map5.p1, .map5.p2 {
	width: 33%;
}

.map5.p3, .map5.p4 {
	width: 49.75vw;
}

.map5.p0{
	left:0;
}
.map5.p1{
	left: 33.5%;
}
.map5.p2{
	right: 0;
}

.map5.p3{
	bottom: 0;
	left: 0;
}
.map5.p4{
	bottom: 0;
	right: 0;
}

/*
