button.item {
    min-height: 55px;
    max-height: 200px;
    height:12vh;
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
    min-width: 8vw;
    max-width: 300px;
    margin: 15px 10px;
    padding-right: 25px;
    padding-left: 25px;
    background-position: center;
    background-size: cover;
    cursor: pointer;
    display:-ms-inline-flexbox;
    display:inline-flex;
  	-ms-flex-pack: center;
  	    justify-content: center;
  	-ms-flex-align: center;
  	    align-items: center;
    position: relative;
    border: none;
}

button.item .button {
  position: absolute;
  top: 0;
  right: 0;
}
button.item .textfit, button.item div.label {
	color: #FF9900;
	width:100%;
	-ms-flex: none;
	    flex: none;
	font-size: 2.8em;
	white-space: normal;
	text-shadow: black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.2em;
	-webkit-user-select: none;  
  	-moz-user-select: none;     
  	-ms-user-select: none;      
  	user-select: none;          
}

button.item.on {
	-webkit-box-shadow: 0px 0px 0px 6px #333333;
	        box-shadow: 0px 0px 0px 6px #333333;
	-webkit-transition-property: -webkit-box-shadow;
	transition-property: -webkit-box-shadow;
	-o-transition-property: box-shadow;
	transition-property: box-shadow;
	transition-property: box-shadow, -webkit-box-shadow;
	-webkit-transition-duration: 0.25s;
	     -o-transition-duration: 0.25s;
	        transition-duration: 0.25s;
	-webkit-transition-timing-function: linear;
	     -o-transition-timing-function: linear;
	        transition-timing-function: linear;
}

button.item.off {
	-webkit-box-shadow: 0px 0px 0px 0px #333333;
	        box-shadow: 0px 0px 0px 0px #333333;
	-webkit-transition-property: -webkit-box-shadow;
	transition-property: -webkit-box-shadow;
	-o-transition-property: box-shadow;
	transition-property: box-shadow;
	transition-property: box-shadow, -webkit-box-shadow;
	-webkit-transition-duration: 0.25s;
	     -o-transition-duration: 0.25s;
	        transition-duration: 0.25s;
	-webkit-transition-timing-function: linear;
	     -o-transition-timing-function: linear;
	        transition-timing-function: linear;
}


