.ViewBar-container {
	/*display: flex;*/
	background-color: #FF6600;
	position: relative;
}

.ViewBar-container .flip-move {
	/*height:30vh;  grid */
	/*min-height: 100px; grid */

	/*width:100%;*/
	/*position: fixed;*/
	overflow-x: auto;
	/*overflow-y: hidden;*/
	/*white-space: nowrap;*/
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap:nowrap;
	    flex-wrap:nowrap;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: normal;
	    justify-content: normal;
	-webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    margin-left: 20px;
    margin-right:20px;
}



.bottom {
	bottom:0;
}



